<template>
    <div class="add-product">
        <div class="choose-type">
            选择服务产品类型：
            <Select v-model="productType" @on-change="goToAddProduct">
                <Option value="transport">运输产品</Option>
                <Option value="site">场所产品</Option>
                <Option value="customs">关务产品</Option>
                <Option value="cpa">综保区产品</Option>
            </Select>
        </div>
        <div class="white-bg">
            <div class="add-site">
                <Row>
                    <Col span="3">
                        <div class="name">场所:</div>
                    </Col>
                    <Col span="21">
                        <Form
                        ref="formValidate"
                        inline
                        :model="siteData"
                        :rules="ruleValidate"
                        :label-colon="true"
                        label-position="left"
                        :label-width="100">
                            <FormItem label="国家地区" prop="dispatchSite">
                                <Select v-model="siteData.dispatchCity">
                                    <Option value="1">运输产品</Option>
                                    <Option value="2">场所产品</Option>
                                    <Option value="3">关务产品</Option>
                                    <Option value="4">综保区产品</Option>
                                </Select>
                            </FormItem>
                            <FormItem label="场所名称" prop="destinationSite">
                                <Select v-model="siteData.destinationCity">
                                    <Option value="1">运输产品</Option>
                                    <Option value="2">场所产品</Option>
                                    <Option value="3">关务产品</Option>
                                    <Option value="4">综保区产品</Option>
                                </Select>
                            </FormItem>
                        </Form>
                    </Col>
                </Row>
            </div>
            <div class="choose-parameter">
                <h6 style="margin-top: 0;">选择参数：</h6>
                <div class="container">
                    <Form
                        ref="parameterFormValidate"
                        inline
                        :model="parameterData"
                        :label-colon="true"
                        label-position="left"
                        :label-width="90">
                        <FormItem label="运输类型" prop="transportType">
                            <Select v-model="parameterData.transportType">
                                <Option value="1">公路</Option>
                                <Option value="2">铁路</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="集装箱尺寸" prop="containerSize">
                            <Select v-model="parameterData.containerSize">
                                <Option value="1">公路</Option>
                                <Option value="2">铁路</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="集装箱类型" prop="containerType">
                            <Select v-model="parameterData.containerType">
                                <Option value="1">公路</Option>
                                <Option value="2">铁路</Option>
                            </Select>
                        </FormItem>
                    </Form>
                    <div class="btns">
                        <Button @click="handleReset()">重置参数</Button>
                        <Button type="primary" @click="generateProduct()">保存参数生成产品</Button>
                    </div>
                </div>
            </div>
            <div class="public-component-line"></div>
            <adt-button :allDelShow="true" :delUrl="delUrl" :selectList="selectList"></adt-button>
            <div class="product-list public-modify-record">
                <div class="collapse">
                    <Collapse v-model="curCollapse" accordion>
                        <template v-for="(product, index) in productAllInfo">
                            <Checkbox v-model="product.checked" @on-change="chooseProduct(product.id, product.checked)"></Checkbox>
                            <Panel :name="index + ''" @click="curCollapse = index">
                                <Row>
                                    <Col span="2" class="product-number">
                                        {{ `产品${index + 1}` }}
                                    </Col>
                                    <Col span="10" class="parameter">
                                        产品参数：{{ product.parameters }}
                                    </Col>
                                    <Col span="12" class="btns" v-if="index === productAllInfo.length - 1">
                                        <!-- <Button size="small" @click.stop="setDeadLine">设置产品截止时间</Button> -->
                                        <Button size="small" @click.stop="saveProduct(product)">保存产品</Button>
                                    </Col>
                                </Row>
                                <div slot="content">
                                    <div class="product-content">
                                        <adt-tab-btns :titleNames="product.sceneNames" :isSmallBtn="true" :btnIndex="product.sceneTabIndex" :productIndex="index" @handleBtn="handleSceneBtn" class="scene-tab"></adt-tab-btns>
                                        <!-- 循环场景，默认选中第一个场景 -->
                                        <div v-for="(item, idx) in product.sceneNames" :key="idx" v-show="item === curSceneName || (!product.sceneTabIndex && idx === 0)">
                                            <adt-tab-btns :titleNames="titleNames" :isUnderline="true" :btnIndex="tabIndex" @handleBtn="handleBtn" :width="250" :height="40" class="task-tab"></adt-tab-btns>
                                            <div class="basic-item" v-show="isBasicTask">
                                                <Row class="item-container">
                                                    <Col span="10" class="item">
                                                        基础服务总费用：
                                                        <Input v-model.number="product.scenes[idx].basicTask.cost" />
                                                        元/标箱
                                                    </Col>
                                                    <Col span="14" class="item">
                                                        基础服务总时长：
                                                        <Select v-model="product.scenes[idx].basicTask.time">
                                                            <Option value="1">10</Option>
                                                            <Option value="2">20</Option>
                                                        </Select>
                                                        <Select v-model="product.scenes[idx].basicTask.unit" style="width: 80px;">
                                                            <Option value="1">天</Option>
                                                            <Option value="2">小时</Option>
                                                        </Select>
                                                    </Col>
                                                </Row>
                                                <adt-task-content :basicTask="basicTask" :isAdditionalTasks="false" />
                                            </div>
                                            <div class="additional-item" v-show="!isBasicTask">
                                                <h6>选择附加任务:</h6>
                                                <div class="task-container">
                                                    <!-- 一行放2个放不下，不能按照设计图来 -->
                                                    <div class="task-item">
                                                        <Checkbox></Checkbox>
                                                        <div class="task-name">附加任务1</div>
                                                        <div class="cost">
                                                            费用：
                                                            <Input v-model.number="product.scenes[idx].additionalTask.cost"/>
                                                            元
                                                        </div>
                                                        <div class="time">
                                                            时长：
                                                            <Select v-model="product.scenes[idx].additionalTask.time">
                                                                <Option value="1">10</Option>
                                                                <Option value="2">20</Option>
                                                            </Select>
                                                            <Select v-model="product.scenes[idx].additionalTask.unit" style="width: 80px;">
                                                                <Option value="1">天</Option>
                                                                <Option value="2">小时</Option>
                                                            </Select>
                                                        </div>
                                                    </div>
                                                    <div class="task-item">
                                                        <Checkbox></Checkbox>
                                                        <div class="task-name">附加任务1</div>
                                                        <div class="cost">
                                                            费用：
                                                            <Input v-model.number="cost"/>
                                                            元
                                                        </div>
                                                        <div class="time">
                                                            费用：
                                                            <Select v-model="time">
                                                                <Option value="1">10</Option>
                                                                <Option value="2">20</Option>
                                                            </Select>
                                                            <Select v-model="unit" style="width: 80px;">
                                                                <Option value="1">天</Option>
                                                                <Option value="2">小时</Option>
                                                            </Select>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Panel>
                        </template>
                    </Collapse>
                </div>
            </div>
        </div>
        <!-- 设置产品截止时间modal -->
        <!-- <adt-my-modal
            title="设置产品截止时间"
            :showModal="isModalShow"
            @on-visible-change="visibleChange"
            @on-ok="confirmSetDeadLine()">
            <Form
                ref="deadLineFormValidate"
                inline
                :model="deadLineData"
                :rules="ruleValidate"
                :label-colon="true"
                label-position="left">
                <FormItem label="方案报价截止时间" :label-width="130" prop="time">
                    <DatePicker type="date" @on-change=""></DatePicker>
                </FormItem>
                <FormItem label="方案截止时间几天前开始提醒" :label-width="200" prop="remindTime">
                    <Select v-model="productType.remindTime" style="width: 80px;">
                        <Option value="1">1天</Option>
                        <Option value="2">2天</Option>
                    </Select>
                </FormItem>
            </Form>
        </adt-my-modal> -->
        <!-- 查看全部场景modal -->
        <adt-my-modal
            title="选择产品场景"
            :showModal="isSceneModalShow"
            @on-visible-change="sceneVisibleChange"
            @on-ok="confirmChooseScene()">
            <Form
                ref="deadLineFormValidate"
                inline
                :model="deadLineData"
                :label-colon="true"
                label-position="left">
                <FormItem>
                    <CheckboxGroup v-model="chosenScene" class="scene-checkbox">
                        <Checkbox v-for="(item, index) in 10" :key="index" :label="`${item}~场景`">{{ `场景${index}` }}</Checkbox>
                    </CheckboxGroup>
                </FormItem>
            </Form>
        </adt-my-modal>
    </div>
</template>

<script>
    import addProductContent from '@/mixins/addProductContent'
    import AdtTaskContent from '@/components/adt-task-content/Index'
    import validateRules from '@/libs/validateRules'
    export default {
        mixins: [addProductContent],
        components: {
            AdtTaskContent
        },
        data () {
            return {
                deadLineData: {},
                delUrl: function () {},
                productType: 'site',
                siteData: {
                    dispatchSite: '',
                    destinationSite: ''
                },
                ruleValidate: {
                    dispatchSite: [
                        validateRules.select('启运地')[0]
                    ],
                    destinationSite: [
                        validateRules.select('目的地')[0]
                    ]
                    // dispatchCity: [
                    //     validateRules.select('启运地城市')[0]
                    // ],
                    // destinationCity: [
                    //     validateRules.select('目的地城市')[0]
                    // ]
                },
                parameterData: {
                    way: ''
                },
                cost: '',
                time: '',
                unit: '',
                dispatchLoading: false,
                destinationLoading: false,
                dispatchOptions: [],
                destinationOptions: []
            }
        },
        methods: {
            confirmSetDeadLine () {

            },
            setDeadLine () {
                this.isModalShow = true
            },
            showAllScene () {
                this.isSceneModalShow = true
            },
            dispatchRemoteMethod () {

            },
            destinationRemoteMethod () {

            },
            // 保存产品
            saveProduct (product) {
                console.log(product)
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>
