export default {
    data () {
        return {
            isSceneModalShow: false,
            titleNames: ['设置基础任务费用时长', '设置附加任务费用时长'],
            curSceneName: '',
            sceneTabIndex: 1,
            tabIndex: 1,
            isBasicTask: true,
            basicTask: ['fdfd', '发个施工队'],
            chosenScene: [],
            curCollapse: '',
            curProductIndex: '',
            // 所有产品的参数信息、场景、每个场景对应的基础任务（附加任务）、产品截止时间都放在一起
            // 因为新增页面可以新增多个产品
            productAllInfo: [
                {
                    // 保存产品后端返回id，用于删除
                    id: '1',
                    // 第几个场景
                    sceneTabIndex: 0,
                    // 参数
                    parameters: '',
                    // 选择的场景的名称
                    // 传到tab组件
                    sceneNames: [],
                    // 所有场景下的基础任务、附加任务
                    // 提前将数据结构写好，后续就不用重新去拼凑数据
                    scenes: [
                        {
                            // 场景名称
                            sceneName: '场景1',
                            sceneId: 1,
                            // 基础任务
                            basicTask: {

                            },
                            // 附加任务
                            additionalTask: {

                            }
                        }
                    ],
                    // 产品截止时间
                    productDeadLine: {
                        deadLine: '',
                        remindTime: ''
                    }
                }
            ],
            selectList: {
                ids: []
            }
        }
    },
    methods: {
        sceneVisibleChange (changeStatus) {
            this.isSceneModalShow = changeStatus
        },
        // 切换场景
        handleSceneBtn (index, sceneName, productIndex) {
            this.productAllInfo[productIndex].sceneTabIndex = ++index
            this.curSceneName = sceneName
            this.curProductIndex = productIndex
        },
        // 切换任务
        handleBtn (index) {
            this.tabIndex = index + 1
            // 基础任务
            if (index === 0) {
                this.isBasicTask = true
            } else {
                // 附加任务
                this.isBasicTask = false
            }
        },
        // 重置
        handleReset () {
            this.$refs.parameterFormValidate.resetFields()
        },
        // 保存参数生成产品
        generateProduct () {
            this.$refs.formValidate.validate((valid) => {
                // if (valid) {
                const parameters = Object.values(this.parameterData)
                // 判断是否选择了参数
                const hasParameter = parameters.some(item => item)
                if (!hasParameter) {
                    this.$Message.warning('请先选择参数！')
                    return
                }
                // 判断参数是否完全重复，需要发送ajax请求让后端来判断
                // 因为有的产品已经在提前新增过了，这里拿不到其他产品的参数信息
                // 将参数value从数据中提取出来
                const data = {}
                for (const key in this.parameterData) {
                    // 点击重置后，this.parameterData[key]为undefined
                    data[key] = this.parameterData[key] && this.parameterData[key].split('~')[0]
                }
                // api.then(({ data }) => {
                //     if (false) {
                //         this.$Message.warning('你已经创建同参数产品，不能重复创建！')
                //         return
                //     }
                // })
                this.isSceneModalShow = true
                // }
            })
        },
        // 确认选择场景
        confirmChooseScene () {
            if (!this.chosenScene.length) {
                this.$Message.warning('请至少选择一个场景！')
            } else {
                // 将参数名称从数据中提取出来
                const parameters = []
                for (const val of Object.values(this.parameterData)) {
                    // 点击重置后，this.parameterData[key]为undefined
                    // 去除undefined
                    if (val) {
                        parameters.push(val.split('~')[1])
                    }
                }
                this.productAllInfo.push({
                    parameters: parameters.join('+'),
                    productDeadLine: {}
                })
                const [scenes, sceneNames] = [[], []]
                for (const scene of this.chosenScene) {
                    const [sceneName, sceneId] = [scene.split('~')[0], scene.split('~')[1]]
                    sceneNames.push(sceneName)
                    scenes.push({
                        sceneName,
                        sceneId,
                        basicTask: {},
                        additionalTask: {}
                    })
                }
                this.productAllInfo[this.productAllInfo.length - 1].scenes = scenes
                this.productAllInfo[this.productAllInfo.length - 1].sceneNames = sceneNames
                this.isSceneModalShow = false
            }
        },
        // 选择产品（用于删除）
        chooseProduct (id, status) {
            const ids = this.selectList.ids || []
            // 选中
            if (status) {
                ids.push(id)
            } else {
                // 取消选中
                const index = ids.indexOf(id)
                ids.splice(index, 1)
            }
            this.selectList.ids = ids
        },
        // 选择服务产品类型
        // 跳转到对应的新增页面
        goToAddProduct (val) {
            this.$router.push(`/product-editor/add-${val}-product`)
        }
    }
}
